<template>
<v-container>
    <v-data-table :headers="headers" :items="products" :loading="isLoading" :search="search" sort-by="name" class="elevation-1">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <v-toolbar-title>Products Sell / Buy</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">New Product</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">New Product</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-text-field v-model="editedItem.item" label="Name"></v-text-field>
                                <v-text-field label="Date" v-model="editedItem.date" type="date"></v-text-field>
                                <v-text-field @keyup="uppercase" v-model="editedItem.truckNo" label="Truck No"></v-text-field>
                                <v-text-field v-model="editedItem.from" label="From"></v-text-field>
                                <v-text-field v-model="editedItem.to" label="To"></v-text-field>
                                <v-text-field v-model="editedItem.quantity" type="number" label="Quantity"></v-text-field>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
    </v-data-table>
    <br />
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
</v-container>
</template>

<script>
import axios from "../../axios";

export default {
    data() {
        return {
            dialog: false,
            search: "",
            headers: [{
                    text: "Name",
                    align: "start",
                    sortable: false,
                    value: "item"
                },
                {
                    text: "Date",
                    value: "date"
                },
                {
                    text: "Truck No",
                    value: "truckNo"
                },
                {
                    text: "From",
                    value: "from"
                }, ,
                {
                    text: "To",
                    value: "to"
                }, ,
                {
                    text: "Quantity",
                    value: "quantity"
                },
                {
                    text: "Actions",
                    value: "actions",
                    sortable: false
                }
            ],
            snackbar: false,
            message: "",
            color: "",
            products: [],
            editedIndex: -1,
            editedItem: {
                item: null,
                date: null,
                truckNo: null,
                from: null,
                to: null,
                quantity: null
            },
            isLoading: false,
            defaultItem: {
                item: null,
                date: null,
                truckNo: null,
                from: null,
                to: null,
                quantity: null
            }
        };
    },
    async created() {
        this.isLoading = true;
        axios()
            .get("/api/allProducts")
            .then(response => {
                this.isLoading = false;
                this.products = response.data;
            })
            .catch(err => {
                this.snackbar = true;
                this.message = "Cannot retrieve data at this time";
                this.isLoading = false;
                this.color = "error";
            });
    },
    methods: {
        uppercase() {
            this.editedItem.truckNo = this.editedItem.truckNo.toUpperCase();
        },
        deleteItem(item) {
            this.isLoading = true;
            const index = this.products.indexOf(item);
            confirm("Are you sure you want to delete this item?") &&
                axios()
                .get(`/api/deleteProduct/${this.products[index]._id}`)
                .then(response => {
                    this.products.splice(index, 1);
                    this.isLoading = false;
                })
                .catch(error => {
                    this.snackbar = true;
                    this.message = "Unable to Delete";
                    this.color = "error";
                    this.isLoading = false;
                });
        },
        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        save() {
            console.log(this.editedItem);
            if (
                this.editedItem.name !== null &&
                this.editedItem.date !== null &&
                this.editedItem.truckNo !== null &&
                this.editedItem.from !== null &&
                this.editedItem.to !== null &&
                this.editedItem.quantity !== null
            ) {
                if (this.editedIndex > -1) {
                    Object.assign(this.products[this.editedIndex], this.editedItem);
                    this.close();
                } else {
                    axios()
                        .post("/api/addProduct", this.editedItem)
                        .then(response => {
                            this.snackbar = true;
                            this.color = "green";
                            this.message = "Added";
                            this.isLoading = false;
                        })
                        .catch(error => {
                            this.snackbar = true;
                            this.message = "Failed to Add";
                            this.color = "error";
                            this.isLoading = false;
                        });
                    this.products.push(this.editedItem);
                    this.close();
                }
            } else {
                this.snackbar = true;
                this.color = "error";
                this.message = "All Fields are required";
                return false;
            }
        }
    }
};
</script>
