<template>
<v-container>
    <v-data-table :headers="headers" :items="transactions" :loading="isLoading" :search="search" sort-by="name" class="elevation-1">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <v-toolbar-title>All Transactions</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">New Transaction</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">New Transaction</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-text-field v-model="editedItem.name" label="Name"></v-text-field>
                                <v-text-field label="Date" v-model="editedItem.date" type="date"></v-text-field>
                                <v-text-field v-model="editedItem.reason" label="Reason"></v-text-field>
                                <v-text-field v-model="editedItem.amount" label="Amount"></v-text-field>
                                <v-select :items="items" v-model="editedItem.type" label="Transaction Type"></v-select>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
        <template v-slot:item.type="{ item }">
            <v-chip :color="getColor(item.type)" dark>{{ item.amount }}</v-chip>
        </template>
    </v-data-table>
    <br />
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
    <p>
        Transaction Send :
        <v-chip color="red" dark>{{transactionsSend}}</v-chip>
    </p>
    <p>
        Transaction Received :
        <v-chip color="green" dark>{{transactionsReceived}}</v-chip>
    </p>
</v-container>
</template>

<script>
import axios from "../../axios";

export default {
    data: () => ({
        dialog: false,
        items: ["Cash In", "Cash Out"],
        search: "",
        headers: [{
                text: "Name",
                align: "start",
                sortable: false,
                value: "name"
            },
            {
                text: "Date",
                value: "date"
            },
            {
                text: "Reason",
                value: "reason"
            },
            {
                text: "Amount",
                value: "type"
            },
            {
                text: "Actions",
                value: "actions",
                sortable: false
            }
        ],
        snackbar: false,
        message: "",
        color: "",
        transactions: [],
        editedIndex: -1,
        transactionsReceived: 0,
        transactionsSend: 0,
        editedItem: {
            date: null,
            name: null,
            reason: null,
            amount: null,
            type: null
        },
        isLoading: false,
        defaultItem: {
            date: null,
            name: null,
            reason: null,
            amount: null,
            type: null
        }
    }),
    created() {
        this.isLoading = true;
        axios()
            .get("/api/allTransactions")
            .then(response => {
                this.isLoading = false;
                this.transactions = response.data;
                var data = response.data;
                for (var i = 0; i < data.length; i++) {
                    if (data[i].type == "Cash In") {
                        this.transactionsReceived += parseInt(data[i].amount);
                    } else {
                        this.transactionsSend += parseInt(data[i].amount);
                    }
                }
            })
            .catch(err => {
                this.snackbar = true;
                this.message = "Cannot retrieve data at this time";
                this.isLoading = false;
                this.color = "error";
            });
    },
    watch: {
        dialog(val) {
            val || this.close();
        }
    },
    methods: {
        deleteItem(item) {
            const index = this.transactions.indexOf(item);
            confirm("Are you sure you want to delete this item?") &&
                axios()
                .get(`/api/delete/${this.transactions[index]._id}`)
                .then(response => {
                    if (item.type == "Cash In") {
                        this.transactionsReceived -= item.amount;
                    } else {
                        this.transactionsSend -= item.amount;
                    }
                    this.transactions.splice(index, 1);
                })
                .catch(error => {
                    this.snackbar = true;
                    this.message = "Unable to Delete";
                    this.color = "error";
                    this.isLoading = false;
                });
        },

        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        save() {
            if (
                this.editedItem.name !== null &&
                this.editedItem.date !== null &&
                this.editedItem.reason !== null &&
                this.editedItem.amount !== null &&
                this.editedItem.type !== null
            ) {
                if (this.editedIndex > -1) {
                    Object.assign(this.transactions[this.editedIndex], this.editedItem);
                    this.close();
                } else {
                    axios()
                        .post("/api/addTransaction", this.editedItem)
                        .then(response => {
                            this.snackbar = true;
                            this.color = "green";
                            this.message = "Added";
                            this.isLoading = false;
                        })
                        .catch(error => {
                            this.snackbar = true;
                            this.message = "Failed to Add";
                            this.color = "error";
                            this.isLoading = false;
                        });
                    if (this.editedItem.type == "Cash In") {
                        this.transactionsReceived += parseInt(this.editedItem.amount);
                    } else {
                        this.transactionsSend += parseInt(this.editedItem.amount);
                    }
                    this.transactions.push(this.editedItem);
                    this.close();
                }
            } else {
                this.snackbar = true;
                this.color = "error";
                this.message = "All Fields are required";
                return false;
            }
        },
        getColor(type) {
            if (type == "Cash In") return "green";
            else return "red";
        }
    }
};
</script>
